.loginBody {
    background-image: url("pic1.jpg");
    background-size: cover;
    background-position: center;
    height: 750px;
    border: 0.1px dotted grey;
}

.loginbox {
    font-family: sans-serif;
    width: 320px;
    height: 500px;
    background: #000;
    color: #fff;
    top: 60%;
    left: 50%;
    position: absolute;
    transform: translate(-50%,-50%);
    padding: 70px 30px;
}

.avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    position: absolute;
    top: -50px;
    left: calc(50% - 50px);
}

h1 {
    margin: 0;
    padding: 0 0 20px;
    text-align: center;
    font-size: 22px;
}

.loginbox p {
    margin: 0;
    padding: 0;
    font-weight: bold;
}

.loginbox input {
    width: 100%;
    margin-bottom: 20px;
}

.loginbox input[type="text"], input[type="password"] {
    border: none;
    border-bottom: 1px solid #fff;
    background: transparent;
    outline: none;
    height: 40px;
    color: #fff;
    font-size: 16px;
}

.loginbox .button {
    border: none;
    outline: none;
    height: 40px;
    background: rgb(244, 115, 66);
    color: #fff;
    font-size: 18px;
    border-radius: 20px;
    margin-bottom: 20px;
}

.loginbox .button:hover {
    cursor: pointer;
    background: #ffc107;
    color: #000;
}

.loginbox a {
    text-decoration: none;
    font-size: 12px;
    line-height: 20px;
    color: darkgrey;
}

.loginbox a:hover {
    color: #ffc107;
}

#messageArea {
    text-align: center;
    color: darkorange;
}
