.textContainer {
    width: 55%;
    margin: auto;
    font-family: Futura, "Trebuchet MS";
    line-height: 2em;
    margin-top: 60px;
    margin-bottom: 70px;
}
.textContainer h2 {
    font-size: 50px;
    line-height: 1.3em;
}
.textContainer p,ol {
    font-size: 16px;
    color: rgb(130, 130, 130);
}
.imageContainer {
  display: grid;
  grid-template: 1fr / 1fr 1fr 1fr;
  /* border: 5px solid black; */
  justify-content: center;
  justify-items: center;
  align-content: center;
  align-items: center;
  /* grid-row-gap: 40px; */
  grid-column-gap: 30px;
  margin: 40px auto 60px auto;
  padding: 0 15px;
}
#big {
  /* grid-template: repeat(8,1fr) / repeat(3,1fr); */
  grid-template: repeat(4,1fr) / repeat(3,1fr);
       /*************************************/
  /* max-height: 500px; */
  max-width: 1000px;
}
.headRow {
  /* border: 5px solid black; */
  height: 3em;
  margin: 0;
  padding-top: 8px;
  text-align: center;
  font-size: 32px;
}
.head1 {
  grid-area: 1 / 1 / 2 / 2;
}
.head2 {
  grid-area: 1 / 2 / 2 / 3;
}
.head3 {
  grid-area: 1 / 3 / 2 / 4;
}
.imageRow {
    margin: 0;
    padding: 0;
    /* border: 5px solid black; */
    width: 100%;
    border-radius: 5px;
}
.learnRow {
    color: white;
    text-align: center;
    /* align-self: start; */
    font-family: "futura-pt", verdana;
    /* border: 5px solid black; */
    margin: 0;
    padding: 0;
    font-size: 30px;
    letter-spacing: 2px;
    padding-top: 40%;
    border-radius: 5px;
    opacity: 0;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    background-color: black;
    height: 100%;
    text-align: center;
}
.innerContainer1 {
  grid-area: 2 / 1 / 5 / 2;
  display: grid;
  grid-template: repeat(5, 1fr) / 1fr;
  width: auto;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.15);
  -webkit-transition: box-shadow 0.25s ease-in;
  transition: box-shadow 0.25s ease-in;
}
.img1 {
  grid-area: 1 / 1 / 6 / 2;
}
.learn1 {
  grid-area: 1 / 1 / 6 / 2;
}
.innerContainer1:hover .learn1 {
  opacity: 0.7;
  cursor: pointer;
}
.innerContainer2 {
  grid-area: 2 / 2 / 5 / 3;
  display: grid;
  grid-template: repeat(5, 1fr) / 1fr;
  width: auto;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.15);
}
.img2 {
  grid-area: 1 / 1 / 6 / 2;
}
.learn2 {
  grid-area: 1 / 1 / 6 / 2;
}
.innerContainer2:hover .learn2 {
  opacity: 0.7;
  cursor: pointer;
}
.innerContainer3 {
  grid-area: 2 / 3 / 5 / 4;
  display: grid;
  grid-template: repeat(5, 1fr) / 1fr;
  width: auto;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.15);
}
.img3 {
  grid-area: 1 / 1 / 6 / 2;
}
.learn3 {
  grid-area: 1 / 1 / 6 / 2;
}
.innerContainer3:hover .learn3 {
  opacity: 0.7;
  cursor: pointer;
}
