    top: 150px;
  }
}
@media screen and (max-width: 730px) {
  .inPicHeading {
    top: 120px;
  }
}
@media screen and (max-width: 650px) {
  .inPicHeading {
    top: 130px;
  }
  .imageContainer {
    display: grid;
    grid-template: 1fr 1fr 1fr / 1fr;
    /* border: 5px solid black; */
    justify-content: center;
    justify-items: center;
    align-content: center;
    align-items: center;
    margin: 40px auto 60px auto;
    padding: 0;
  }
  .imageContainer, .big {
    grid-template: repeat(14,1fr) / 1fr;
  }
  .head1 {
    grid-area: 1 / 1 / 2 / 2;
  }
  .innerContainer1 {
    grid-area: 2 / 1 / 5 / 2;
    height: auto;
  }
  .head2 {
    grid-area: 6 / 1 / 7 / 2;
  }
  .innerContainer2 {
    grid-area: 7 / 1 / 10 / 2;
    height: 100%;
  }
  .head3 {
    grid-area: 11 / 1 / 12 / 2;
  }
  .innerContainer3 {
    grid-area: 12 / 1 / 15 / 2;
    height: 100%;
  }
  .imageRow {
    width: 100%;
    /* border: 5px solid red; */
  }
  .learnRow {
    padding-top: 40%;
  }
}

.slideshow-container {
  display: grid;
  grid-template: repeat(7, 1fr) / repeat(10, 1fr);
  width: 90%;
  max-height: 700px;
  /* min-height: 600px; */
  margin: auto;
  background-color: black;
}
.portrait{
  grid-area: 2 / 4 / 7 / 8;
  /* background: grey; */
}
.portrait img{
  height: 100%;
  display: block;
  margin: auto;
}
.landscape {
  grid-area: 2 / 2 / 6 / 10;
}
.landscape img{
  /* width: 100%; */
  height: 100%;
  display: block;
  margin: auto;
}
.prev {
  grid-area: 4 / 1 / 5 / 2;
  justify-self: center;
  align-self: center;
}
.next {
  grid-area: 4 / 10 / 5 / 11;
  justify-self: center;
  align-self: center;
}
.numbertext {
  grid-area: 1 / 10 / 2 / 11;
  color: #f2f2f2;
  font-size: 16px;
  /* padding: 10px 12px; */
  justify-self: start;
  align-self: center;
}
.text {
  grid-area: 7 / 1 / 8 / 11;
  text-align: center;
  align-self: center;
  color: #f2f2f2;
  font-size: 15px;
}
.prev:hover, .next:hover {
    padding: 10px 10px;
    background-color: #f1f1f1;
    color: black;
    border-radius: 3px;
}
.prev, .next {
    cursor: pointer;
    /* position: relative; */
    /* width: auto;
    padding: 16px;
    margin-top: -22px; */
    color: white;
    font-weight: bold;
    font-size: 18px;
    -webkit-transition: 0.6s ease;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    /* border: 5px solid red; */
}
@media screen and (max-width: 1004px) {
  .slideshow-container {
    max-height: 600px;
  }
  .landscape img {
    width: 100%;
  }
}
@media screen and (max-width: 808px) {
  .slideshow-container {
    max-height: 500px;
  }
  .portrait img {
    width: 100%;
  }
}

ul {
