.month {
    padding: 70px 25px;
    width: 100%;
    background: #1abc9c;
    text-align: center;
    /* border: 5px solid red; */
    /* height: 20em; */
    /* display: grid;
    grid-template: repeat(3, 1fr) / repeat(5, 1fr); */
}

.month ul {
    margin: 0;
    padding: 0;
}

.month ul li {
    color: white;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 3px;
}

.month .prevMonth {
    float: left;
    padding-top: 10px;
}

.month .nextMonth {
    float: right;
    padding-top: 10px;
}

.weekdays {
    margin: 0;
    padding: 10px 0;
    background-color: #ddd;
}

.weekdays li {
    display: inline-block;
    width: 13.85%;
    color: #666;
    text-align: center;
    /*border: solid 0.1px black;*/
}

.days {
    padding: 10px 0 0 10px;
    background: #eee;
    margin: 0;
}

.days li {
    list-style-type: none;
    display: inline-block;
    width: 14.2%;
    text-align: left;
    vertical-align: top;
    margin-bottom: 15px;
    font-size: 12px;
    color: #777;
    /*border: solid 0.1px black*/
    /* float: right; */
}

.days li .active {
    padding: 5px;
    background: #1abc9c;
    color: white !important
}

.days li .oneDigit {
    margin-right: 8px;
}

.days li span {
    vertical-align: top;
}

.days li textarea {
    width: 88%;
    /* height: 100%; */
    height: 5em;
    resize: none;
    background: #eee;
}

.button {
    border: none;
    outline: none;
    height: 40px;
    background: #1abc9c;
    color: #fff;
    font-size: 18px;
    border-radius: 20px;
    display: block;
    margin: 10px auto;
}

.button:hover {
    cursor: pointer;
    background: #ffc107;
    color: #000;
}

/* Add media queries for smaller screens */
@media screen and (max-width:720px) {
    .weekdays li, .days li {width: 13.1%;}
}

@media screen and (max-width: 420px) {
    .weekdays li, .days li {width: 12.5%;}
    .days li .active {padding: 2px;}
}

@media screen and (max-width: 290px) {
    .weekdays li, .days li {width: 12.2%;}
}
